<script lang="ts">
  import { onMount } from 'svelte'
  import { Button } from 'spaper'
  import { selectedAccount } from '../store'
  import {
    getPoolCount,
    getPoolInfo,
    getPendingRewards,
    harvest,
 } from '../helpers/eth-helper'
 import { handleTxWithUx } from '../helpers/tx-helper'

  let poolsCount: number
  // FIXME: type
  let pools: any[] = []

  onMount(async () => {
    poolsCount = await getPoolCount();
    selectedAccount.subscribe(async (account) => {
      for (let i=0; i<poolsCount; i++) {
        pools[i] = {
          pid: i,
          rewards: await getPendingRewards(i, account),
        };
      }
    })
  })

  async function harvestHandler(pid: number) {
    console.log('harvesting', pid)
    await handleTxWithUx(
      `Harvesting #${pid}`,
      harvest, [pid],
    )
  }

  $: activePools = pools.filter(pool => pool.rewards)
</script>

<h1>the farms</h1>

{#each activePools as { pid, rewards }}
  <div>
    <h4>#{pid}</h4>
    <p>pending rewards: ~{rewards} PANTHER</p>

    <Button on:click={harvestHandler.bind(null, pid)}>
      Harvest
    </Button>
  </div>
{/each}
